/* end general */

/* start of the header wrapper */
.header-wrapper nav ul li a.active
{
  color: rgb(10, 106, 184);
} 
.header-wrapper nav ul li a:hover
{
  color: rgb(10, 106, 184);
} 
.header-wrapper nav ul li a 
{
  position: relative;
  font-size: 14px;
  color: #000;
  transition: all .3s ease-in;
}
.header-wrapper nav ul li 
{
  position: relative;
  width: 100%;
} 
.header-wrapper nav ul .dropdown-item a .dropdown-menu .dropdown-item a .dropdown-menu li 
{
  transform: translateX(30px);
}
.header-wrapper nav ul .dropdown-item a .dropdown-menu .dropdown-item a .dropdown-menu
{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  /* margin-left: 120px; */
  margin-left: 80px;
  visibility: hidden;
  opacity: 0;
  transition: .4s ease-in;
}
.header-wrapper nav ul .dropdown-item a .dropdown-menu .dropdown-item:hover a .dropdown-menu
{
  visibility: visible;
  opacity: 1;
  margin-left: 142px;
} 
.header-wrapper nav ul .dropdown-item a .dropdown-menu .dropdown-item 
{
  transform: translateX(0px);
  text-align: end;
}
.header-wrapper nav ul .dropdown-item a .dropdown-menu li:last-child
{
  border: none;
  width: 100%;
  position: relative;
} 
.header-wrapper nav ul .dropdown-item a .dropdown-menu li 
{
  transform: translateX(35px);
  padding-bottom: 10px;
  padding-top: 10px;
}
.header-wrapper nav ul .dropdown-item a .dropdown-menu 
{
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  border: 1px solid rgba(10, 106, 184, 0.474);
  position: absolute;
  z-index: 77;
  background-color: #fff;
  width: 140px;
  /* margin-top: 30px; */
  margin-top: 60px;
  opacity: 0;
  visibility: hidden;
  transition: .4s ease-in;
}
.header-wrapper nav ul .dropdown-item:hover a .dropdown-menu
{
  opacity: 1;
  visibility: visible;
  margin-top: 30px;
}
.header-wrapper nav ul .dropdown-item:hover
{
  background-color: inherit;
} 
.header-wrapper nav ul .dropdown-item 
{
  margin-right: 25px;
}
.header-wrapper nav ul .dropdown-item a .fa
{
  margin-left: 10px;
} 
.header-wrapper nav ul 
{
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  list-style: none;
  width: 60%;
}
.header-wrapper nav .size-icons a:hover
{
  background-color: rgb(11, 131, 230);
} 
.header-wrapper nav .size-icons a 
{
  color: #fff;
  background-color: rgb(10, 106, 184);
  padding: 8px 20px;
  transition: .3s ease-in-out;
  border-radius: 5px;
}
.header-wrapper .size-icons .fa
{
  display: none;
  color: #000;
  font-size: 20px;
  cursor: pointer;
} 
.header-wrapper nav .size-icons 
{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.header-wrapper nav .navbar-brand 
